<template>
  <div class="build1">
    <back-home></back-home>
    <transition name="part1">
      <div class="part1" v-if="part1Show" @mousewheel.prevent="mouseWheel">
        <div class="banner" ref="banner1" :style="{ left: imgLeft + 'px' }">
          <img
            src="../../../assets/buildInfo/build23/素材01.png"
            @load="imgLoad"
          />
        </div>
        <div class="word" ref="word1" :style="{ top: wordTop + 'px' }">
          <p class="pFirst">广府传统婚俗文化你知多少？</p>
          <p>在一排排富有</p>
          <p>民国风情韵味的小洋楼中</p>
          <p>“藏”着一间建成不久的</p>
          <p>广府传统婚俗文化馆</p>
        </div>
      </div>
    </transition>
    <transition name="part2">
      <div class="part2" v-if="part2Show">
        <div class="indexBox build1Shu" @mousewheel="mouseWheel2">
          <img src="@/assets/buildInfo/build25/地图logo.png" class="logo2" />
          <div class="index" ref="index">
            <div class="box1">
              <div class="content1">
                <p style="font-family: QingKai" class="content1P1">
                  卢玮玮·八姑姐
                </p>
                <p class="content1P2">1980年，正值改革开放初期</p>
                <p class="content1P2">广东非物质文化遗产传承人</p>
                <p class="content1P2">广府传统婚俗第四代传承人</p>
              </div>
              <div class="imgBox1">
                <img
                  src="@/assets/buildInfo/build25/业记素材-06.png"
                  alt=""
                  class="img1"
                />
              </div>
            </div>
            <div class="box2">
              <div class="content2">
                <p class="content2P2">那时候的品种只有芝麻糊和绿豆沙</p>
                <p class="content2P2">一个煤炉两个石磨</p>
                <p class="content2P2">解一夏炎热，暖一季秋冬</p>
                <p class="content2P2">香味飘满整条惠吉西</p>
              </div>
              <div class="imgBox2">
                <img
                  src="@/assets/buildInfo/build25/业记素材-07.png"
                  alt=""
                  class="img2"
                />
                <img
                  src="@/assets/buildInfo/build25/业记素材-08.png"
                  alt=""
                  class="img2"
                />
                <img
                  src="@/assets/buildInfo/build25/业记素材-09.png"
                  alt=""
                  class="img2"
                />
              </div>
            </div>
            <div class="box3">
              <div class="content3">
                <p class="content3P3">这里成为了街坊们聊聊家常</p>
                <p class="content3P3">喝喝糖水的小憩之地</p>
              </div>
              <div class="imgBox3">
                <img
                  src="@/assets/buildInfo/build25/业记素材-37.png"
                  alt=""
                  class="img3"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part3">
      <div class="part3" @mousewheel.prevent="mouseWheel3" v-if="part3Show">
        <div
          class="banner banner2"
          ref="banner2"
          :style="{ left: imgLeft2 + 'px', zIndex: 10 }"
        >
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-21.png"
            @load="imgLoad2"
            class="inlineImg"
            v-if="imgShow1"
            @mouseenter="imgEnter2(1)"
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-26.png"
            v-else
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-22.png"
            @load="imgLoad2"
            class="inlineImg"
            v-if="imgShow2"
            @mouseenter="imgEnter2(2)"
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-27.png"
            v-else
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-23.png"
            @load="imgLoad2"
            class="inlineImg"
            v-if="imgShow3"
            @mouseenter="imgEnter2(3)"
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-28.png"
            v-else
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-24.png"
            @load="imgLoad2"
            class="inlineImg"
            v-if="imgShow4"
            @mouseenter="imgEnter2(4)"
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-29.png"
            v-else
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-25.png"
            @load="imgLoad2"
            class="inlineImg"
            v-if="imgShow5"
            @mouseenter="imgEnter2(5)"
          />
          <img
            src="../../../assets/buildInfo/build23/建筑八姑姐 素材-30.png"
            v-else
          />
        </div>
        <div
          class="word"
          ref="word2"
          :style="{ top: wordTop2 + 'px', zIndex: 1 }"
        >
          <p>八姑姐作为广府传统婚俗文化馆的主理人，</p>
          <p>对于传统的婚俗文化了如指掌。</p>
          <p>馆内陈列了传统婚俗用品呈现广府文化魅力。</p>
          <p>馆内的老物件都是她“千方百计”通过</p>
          <p>不同的渠道收集而成的。</p>
          <p>比如从自己家中或亲戚家中找到的礼饼盒、</p>
          <p>电视机；从某交易平台买回来的老式洗衣机</p>
          <p>其中里面部分物品已经有了上百年的历史。</p>
        </div>
      </div>
    </transition>
    <transition name="part4">
      <div class="part4" v-if="part4Show" @mousewheel.prevent="part4Wheel">
        我是文字图片穿插
      </div>
    </transition>
    <transition name="part5">
      <div class="part5" @mousewheel.prevent="part5Wheel" v-if="part5Show">
        <div class="banner" ref="banner5" :style="{ left: imgLeft5 + 'px' }">
          <img
            src="../../../assets/buildInfo/build23/素材7.png"
            @load="imgLoad5"
          />
        </div>
        <div class="word" ref="word5" :style="{ top: wordTop5 + 'px' }">
          <p>在这个流量时代，</p>
          <p>像八姑姐这样坚持发扬</p>
          <p>广府传统文化的人真的很了不起。</p>
          <p>太多年轻人只知表面，却不知其意。</p>
          <p>只有去过才发现不少民俗寓意原来是</p>
          <p>如此有意思有趣。</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BackHome from "../../../components/backHome.vue";
import PoPup from "../../../components/popup.vue";

export default {
  name: "build25",
  data() {
    return {
      wordTop: 0,
      imgLeft: 0,
      imgWidth: null,
      wordHeight: null,
      screenY: null,
      screenX: null,
      Show: false,
      imgLeft2: 0,
      imgWidth2: 0,
      wordTop2: 0,
      wordHeight2: 0,
      indexHeight: 0,
      imgLeft5: 0,
      imgWidth5: 0,
      wordTop5: 0,
      part1Show: true,
      part2Show: false,
      part3Show: false,
      part4Show: false,
      part5Show: false,
      imgNum: 0,
      imgShow1: true,
      imgShow2: true,
      imgShow3: true,
      imgShow4: true,
      imgShow5: true
    };
  },
  components: {
    BackHome,
    PoPup
  },
  mounted() {
    // this.buildIndex = this.$route.query.index; //this.buildIndex是建筑的序号
    this.wordHeight = this.$refs.word1.offsetHeight;
    this.screenX = document.body.clientWidth;
    this.screenY = document.body.clientHeight;
    // this.indexHeight = this.$refs.index.offsetHeight;
  },
  methods: {
    mouseWheel(e) {
      //deltaY-125往上滚
      //下面页面往上顶
      if (e.deltaY > 0) {
        this.wordTop =
          this.wordTop - 15 < -(this.wordHeight - this.screenY)
            ? -(this.wordHeight - this.screenY)
            : this.wordTop - 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      } else {
        //往上滚
        this.wordTop = this.wordTop + 15 > 0 ? 0 : this.wordTop + 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      }
      if (this.wordTop <= -(this.wordHeight - this.screenY)) {
        //下一个页面出现
        this.part2Show = true;
      } else {
        // 第二个页面消失
        this.part2Show = false;
      }
    },
    imgLoad() {
      this.imgWidth = this.$refs.banner1.offsetWidth;
    },
    mouseWheel2(e) {
      if (this.part2Show) {
        this.indexHeight =
          this.indexHeight == 0
            ? this.$refs.index.offsetHeight
            : this.indexHeight;
        if (this.$refs.index.scrollTop == 0 && e.deltaY < 0) {
          //第二part消失
          this.part2Show = false;
        }

        if (
          this.$refs.index.scrollTop >= this.screenY + this.indexHeight - 50 &&
          e.deltaY > 0
        ) {
          //第三part出现
          this.part3Show = true;
        }
      }
    },
    mouseWheel3(e) {
      if (this.part3Show) {
        let a = this.$refs.word2.offsetHeight;
        if (this.wordTop2 == 0 && e.deltaY < 0) {
          //回到第二页
          this.part3Show = false;
        }
        if (e.deltaY > 0) {
          this.wordTop2 =
            this.wordTop2 - 15 < -(a - this.screenY)
              ? -(a - this.screenY)
              : this.wordTop2 - 15;
          this.imgLeft2 =
            (this.wordTop2 * (this.imgWidth2 - this.screenX)) /
            (a - this.screenY);
        }
        if (e.deltaY <= 0) {
          this.wordTop2 = this.wordTop2 + 15 > 0 ? 0 : this.wordTop2 + 15;
          this.imgLeft2 =
            (this.wordTop2 * (this.imgWidth2 - this.screenX)) /
            (a - this.screenY);
        }
        if (this.wordTop2 <= -(a - this.screenY)) {
          //下一个页面出现
          this.part4Show = true;
        } else {
          // 第二个页面消失
          this.part4Show = false;
        }
      }
    },
    imgLoad2() {
      this.imgNum++;
      if (this.imgNum >= 5) {
        this.imgWidth2 = this.$refs.banner2.offsetWidth;
      }
    },
    part4Wheel(e) {
      if (e.deltaY < 0) {
        this.part4Show = false;
      } else {
        this.part5Show = true;
      }
    },
    imgLoad5() {
      this.imgWidth5 = this.$refs.banner5.offsetWidth;
    },
    part5Wheel(e) {
      if (this.part5Show) {
        let a = this.$refs.word5.offsetHeight;
        if (this.wordTop5 == 0 && e.deltaY < 0) {
          //回到第二页
          this.part5Show = false;
        }
        if (e.deltaY > 0) {
          this.wordTop5 =
            this.wordTop5 - 15 < -(a - this.screenY)
              ? -(a - this.screenY)
              : this.wordTop5 - 15;
          this.imgLeft5 =
            (this.wordTop5 * (this.imgWidth5 - this.screenX)) /
            (a - this.screenY);
        }
        if (e.deltaY <= 0) {
          this.wordTop5 = this.wordTop5 + 15 > 0 ? 0 : this.wordTop5 + 15;
          this.imgLeft5 =
            (this.wordTop5 * (this.imgWidth5 - this.screenX)) /
            (a - this.screenY);
        }
      }
    },
    imgEnter2(num) {
      if (num == 1) {
        this.imgShow1 = false;
      } else if (num == 2) {
        this.imgShow2 = false;
      } else if (num == 3) {
        this.imgShow3 = false;
      } else if (num == 4) {
        this.imgShow4 = false;
      } else {
        this.imgShow5 = false;
      }
    },
    isDetail(is) {
      if (is) {
        //在这里要禁用鼠标不能滚动--背景板不能滚动
      } else {
        // 恢复鼠标滚轮事件
      }
    }
  }
};
</script>
<style scoped>
.part1 {
  position: relative;
  background-color: rgb(224, 225, 207);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 0.7s linear;
}
.part2 {
  transition: 0.5s linear;
  position: absolute;
  top: 0;
}
.part3,
.part5 {
  position: absolute;
  top: 0;
  background-color: rgb(229, 207, 188);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 1s linear;
}
.part4 {
  transition: 1s;
  height: 100vh;
  width: 100vw;
  background-color: blue;
  position: absolute;
  top: 0;
  z-index: 101;
}
.part5 {
  background-color: rgb(96, 96, 96);
  color: white;
  transition: 1s;
  z-index: 102;
}
.part1-enter-active,
.part2-enter-active,
.part4-enter-active,
.part5-enter-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part1-leave-active,
.part2-leave-active,
.part4-leave-active,
.part5-leave-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-leave-active {
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-enter-active {
  transition: cubic-bezier(0.01, 0.9, 0.1, 0.1);
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.banner {
  height: 100vh;
  padding: 0 10%;
  position: absolute;
  top: 0;
  transition: 1s;
}
.banner img {
  height: 100vh;
}
.word {
  transition: 1s;
  position: absolute;
  left: 0;
  font-size: 110px;
  width: 100vw;
  padding-top: 50px;
  padding-bottom: 35vh;
}
.word p {
  font-family: QingKai;
  text-align: center;
  margin-top: 30px;
  border: 0;
}
.pFirst {
  font-size: 150px;
}
.build1Shu {
  position: absolute;
  transition: 2s;
}

.indexBox {
  height: 100vh;
}
.index {
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  perspective: 500px;
  transform-style: preserve-3d;
}
.box1,
.box2,
.box3 {
  background-color: #dde5ef;
  width: 100vw;
  height: 100vh;
  position: relative;
}
.logo2 {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  z-index: 5;
}
.content1,
.content2,
.content3 {
  font-family: XiuKai;
  font-size: 35pt;
}
.content1 {
  position: absolute;
  z-index: 10;
  bottom: 10rem;
  left: 15rem;
  transform: translateZ(300px) scale(0.4);
  text-align: left;
}
.content1P1 {
  font-size: 75pt;
}
.content1P2 {
  font-family: XiuKai;
  font-size: 37pt;
}
.imgBox1 {
  width: 76vw;
  height: 83vh;
  position: absolute;
  z-index: 1;
  top: -15%;
  left: 24%;
  transform: translateZ(100px) scale(0.8);
}
.img1 {
  position: absolute;
  right: -43%;
  width: 94vw;
}
.content2 {
  position: absolute;
  top: 5vh;
  font-size: 105pt;
  text-align: center;
  z-index: 10;
  transform: translateZ(300px) scale(0.4);
}
.content2P2 {
  font-size: 105pt;
}

.imgBox2 {
  height: 95vh;
  width: 80%;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -20%, 100px) scale(0.8);
  display: flex;
  justify-content: space-between;
}
.img2 {
  height: 50%;
}
.content3 {
  color: white;
  position: absolute;
  top: 10vh;
  font-size: 100pt;
  text-align: center;
  z-index: 10;
  transform: translateZ(300px) scale(0.4);
}
.imgBox3 {
  width: 90%;
  height: 90vh;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 100px) scale(0.8);
  line-height: 90vh;
}
.img3 {
  width: 90%;
}
.inlineImg {
  display: inline-block;
}
.banner2 {
  display: flex;
}
</style>
